<template>
  <div>
  <h1>图片预览</h1>
    <previewpicture v-model:pictures="pictures1"></previewpicture>
    <previewpicture v-model:pictures="pictures2"></previewpicture>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, toRefs, SetupContext } from "vue";
interface Data {
    pictures1:string[],
    pictures2:string[],
}
export default defineComponent({
  name: "",
  props: {},
  components: {},
  setup(props, ctx: SetupContext) {
    const data: Data = reactive<Data>({
        pictures1:[
          require("@/assets/logo.png"),  
        ],
        pictures2:[
          require("@/assets/logo.png"),  
          require("@/assets/1.a7ce9b96.jpg"),  
          require("@/assets/2.973447f0.jpeg"),  
          require("@/assets/3.65303675.jpg")
        ],
    });
    return {
      ...toRefs(data),
    };
  },
});
</script>

